import React from 'react';

export const Column = (props) => {
    let renderQuestions = props.questions.map(question => {
        return (
        <div className="question_board_question">
            {question.points}
        </div>
        );
    });
    return (<div className="question_board_column">

        <div className="question_board_category">
            {props.category}
        </div>
        {renderQuestions}
    </div>);
};
